<template>
    <a-form @submit="handleSubmit"
            :autoFormCreate="(form)=>{this.form = form}">
        <a-form-item
                label='规划名称'
                :labelCol="{ span: 3 }"
                :wrapperCol="{ span: 15 }"
                fieldDecoratorId="title"
        >
            <a-input/>
        </a-form-item>
        <a-form-item
                label='规划发起人'
                :labelCol="{ span: 3 }"
                :wrapperCol="{ span: 15 }"
                fieldDecoratorId="initiate_user_id"
        >
            <SelectUser/>
        </a-form-item>
        <a-form-item
                label='规划内容'
                :labelCol="{ span: 3 }"
                :wrapperCol="{ span: 15 }"
                fieldDecoratorId="content"
        >
            <a-textarea :autosize="{ minRows: 2, maxRows: 6 }"/>
        </a-form-item>
        <a-form-item
                label='规划时间'
                :labelCol="{ span: 3 }"
                :wrapperCol="{ span: 15 }"
                fieldDecoratorId="published_at"
        >
            <DateTime/>
        </a-form-item>
        <a-form-item
                label='附件'
                :labelCol="{ span: 3 }"
                :wrapperCol="{ span: 15 }"
                fieldDecoratorId="more_files"
        >
            <upload-files/>
        </a-form-item>
        <a-form-item
                :wrapperCol="{ span: 12, offset: 5 }"
        >
            <a-button type='primary' htmlType='submit'>
                提交
            </a-button>
        </a-form-item>
    </a-form>
</template>

<script>
    import {getWorkPlan, createWorkPlan, updateWorkPlan} from '@/api/departmentWorkPlan'
    import SelectUser from '@/components/select/SelectUser'
    import SelectUsers from '@/components/select/SelectUsers'
    import uploadFiles from '@/components/upload/UploadFiles'
    import DateTime from "@/components/select/DateTime";

    export default {
        components: {
            DateTime, SelectUser, SelectUsers, uploadFiles
        },
        props: {
            id: {
                type: Number,
                default: 0,
            }
        },
        data() {
            return {
                log: console.log,
                form: {},
            }
        },
        computed: {},
        methods: {
            init() {
                if (!this.id) {
                    return;
                }

                getWorkPlan(this.id)
                    .then(res => {
                        this.form.setFieldsValue({
                            title: res.data.title,
                            initiate_user_id: res.data.initiate_user_id,
                            content: res.data.content,
                            published_at: res.data.published_at,
                            more_files: res.data.more.hasOwnProperty('files') ? res.data.more.files : null,
                        })
                    })
            },
            beforeSubmit() {
                return true;
                let res = false
                // this.form.validateFields(
                //     (err) => {
                //         if (!err) {
                //             res = true
                //         }
                //     })
                return res
            },
            handleSubmit(e) {
                e.preventDefault();

                let request;

                let data = this.form.getFieldsValue();
                data.published_at = parseInt(moment(data.published_at).valueOf() / 1000)

                if (this.id) {
                    request = updateWorkPlan(this.id, data)
                } else {
                    request = createWorkPlan(data)
                }
                request.then(() => {
                    let msg = this.id ? '更新成功' : '新增成功';
                    this.$message.success(msg);
                })
            },
        },
        mounted() {
            this.init();
        },
    }
</script>
